p {
  font-size: 1em;
  line-height: 1.6;
  padding: 1em;
}

#slider-container {
  display: flex;
  flex-direction: row;
  /* flex-wrap: wrap; */
}

#testimonials {
  position: relative;
  justify-content: center;
  margin: 0 auto;
  overflow: hidden;
  width: 1000px;
  height: 600px;
}

figure {
  position: absolute;
  width: 400%;
  left: 0;
  animation: 20s slider infinite;
  -webkit-animation: 20s slider infinite;
  border-top: 1px solid #c2c1c2;
  border-bottom: 1px solid #c2c1c2;
  margin: 0;
}

blockquote {
  width: 25%;
  margin: 0;
  float: left;
  padding-bottom: 1em;
}

@-webkit-keyframes slider {
  0% {
    transform: translateX(0%);
  }
  25% {
    transform: translateX(0%);
  }
  33% {
    transform: translateX(-25%);
  }
  58% {
    transform: translateX(-25%);
  }
  66% {
    transform: translateX(-50%);
  }
  91% {
    transform: translateX(-50%);
  }
  100% {
    transform: translateX(-75%);
  }
}

@keyframes slider {
  0% {
    transform: translateX(0%);
  }
  25% {
    transform: translateX(0%);
  }
  33% {
    transform: translateX(-25%);
  }
  58% {
    transform: translateX(-25%);
  }
  66% {
    transform: translateX(-50%);
  }
  91% {
    transform: translateX(-50%);
  }
  100% {
    transform: translateX(-75%);
  }
}

footer {
  padding: 1em;
  float: right;
  margin-right: 2em;
}

@media (min-width: 750px) {
  #testimonials {
    height: 150px;
  }
}
